<template>
  <div>
    <div>
      <app-sidebar :messages="messages"></app-sidebar>
    </div>
    <div class="container mt-5">
      <app-content :messages="messages"></app-content>
    </div>
  </div>
</template>

<script>
import Sidebar from "./components/navigation/Sidebar.vue";
import Content from "./components/Content.vue";
import messages from "./data/messages";
import randomMessages from "./data/random-messages";
import { eventBus } from "./main";

export default {
  data() {
    return {
      messages: messages
    }
  },
  components: {
    'app-sidebar': Sidebar,
    'app-content': Content
  },
  created() {
    eventBus.$on('refresh', () => {
      let randomIndex = Math.floor(Math.random() * randomMessages.length), 
      temp = [randomMessages[randomIndex]];
      this.messages = temp.concat(this.messages.splice(0));
    });
    eventBus.$on('sentMessage', (data) => this.messages = [data.message, ...this.messages.slice(0)]);
  }
}
</script>