<template>
    <div>
        <small class="text-muted"> eugevanz@gmail.com </small>
        <h1>{{currentView.title}}</h1>
        <hr/>
        <keep-alive>
            <component :is="currentView.tag" :data="currentView.data"></component>
        </keep-alive>
    </div>
</template>
<script>
import Inbox from "./boxes/Inbox.vue";
import Sent from "./boxes/Sent.vue";
import Important from "./boxes/Important.vue";
import Trash from "./boxes/Trash.vue";
import ViewMessage from "./ViewMessage.vue";
import SendMessage from "./SendMessage.vue";

import { eventBus } from "../main";

export default {
    props: {
        messages: {
            type: Array,
            required: true
        }
    },
    created() {
        eventBus.$on('changeView', data => 
            this.history = [{
                tag: data.tag,
                title: data.title,
                data: data.data || {},
            }].concat(this.history.splice(0))
        );
    },
    data() {
        return {
            history: [
                {
                    tag: 'app-inbox',
                    title: 'Inbox',
                    data: {
                        messages: null
                    }
                }
            ]
        }
    },
    computed: {
        currentView() {
            let current = this.history[0];
            current.data.messages = this.messages;
            return current;
        },
        previousView() {
            if (this.history[1] !== 'undefined') return this.history[1];
        }
    },
    components: {
        'app-inbox': Inbox,
        'app-sent': Sent,
        'app-important': Important,
        'app-trash': Trash,
        'view-message': ViewMessage,
        'send-message': SendMessage
    }
}
</script>