<template>
    <nav class="navbar navbar-expand-lg navbar-dark" style="background-color: #00628B;">
        <div class="container">
            <span class="navbar-brand" href="#">
                <img src="../../assets/logo.png" width="24" height="24" class="d-inline-block align-center" alt=""> Eugene 
            </span>
            <div class="">
                <button class="btn btn-outline-light" title="Refresh" type="button" @click.prevent="refresh"> &#10226; </button>
                <button class="btn btn-outline-light ml-2" title="Compose" type="button" @click.prevent="navigate('send-message','Compose')"> &#9998; </button>
            </div>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
                <ul class="navbar-nav ml-auto">
                    <li class="nav-item" :class="{active: activeView == 'app-inbox'}">
                        <a class="nav-link" href="#" @click.prevent="navigate('app-inbox','Inbox')"> <span v-if="unreadMessages !== 'undefined' && unreadMessages.length != 0" class="badge badge-light"> {{unreadMessages.length}} New </span> Inbox </a>
                    </li>
                    <li class="nav-item" :class="{active: activeView == 'app-sent'}">
                        <a class="nav-link" href="#" @click.prevent="navigate('app-sent','Sent')"> <span class="badge badge-light">{{sentMessages.length}}</span> Sent </a>
                    </li>
                    <li class="nav-item" :class="{active: activeView == 'app-important'}">
                        <a class="nav-link" href="#" @click.prevent="navigate('app-important','Important')"> <span class="badge badge-light">{{importantMessages.length}}</span> Important </a>
                    </li>
                    <li class="nav-item" :class="{active: activeView == 'app-trash'}">
                        <a class="nav-link" href="#" @click.prevent="navigate('app-trash','Trash')"> <span class="badge badge-light">{{trashMessages.length}}</span> Trash </a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>
<script>
import { eventBus } from "../../main";

export default {
    props: {
        messages: {
            type: Array,
            required: true
        }
    },
    created() {
        eventBus.$on('changeView', data => 
            this.activeView = data.tag
        );
    },
    methods: {
        navigate(tagTitle, pageTitle) {
            eventBus.$emit('changeView', {
                tag: tagTitle,
                title: pageTitle
            });
        },
        refresh() {
            eventBus.$emit('refresh');
        }
    },
    data() {
        return {
            activeView: 'app-inbox',
            activeClass: ['active', 'border']
        }
    },
    computed: {
        unreadMessages() {
            return this.messages.filter(message => message.type == 'incoming' && !message.isRead && !message.isDeleted)
        },
        sentMessages() {
            return this.messages.filter(message => message.type == 'outgoing' && !message.isDeleted)
        },
        importantMessages() {
            return this.messages.filter(message => message.type == 'incoming' && !message.isDeleted && message.isImportant)
        },
        trashMessages() {
            return this.messages.filter(message => message.isDeleted === true)
        }
    }
}
</script>