<template>
    <table v-if="messages.length > 0" class="table table-borderless">
        <tbody>
            <tr v-for="message in messages" @click="openMessage(message)" :class="{'table-secondary': typeof message.isRead !== 'undefined' && !message.isRead}" :key="message.date">
                <th><input type="checkbox"/></th>
                <td><a v-if="typeof message.isImportant !== 'undefined'" @click.prevent.stop="message.isImportant = !message.isImportant" class="text-decoration-none text-dark" href="#">&nbsp;
                    <span v-if="message.isImportant != true">&star;</span>
                    <span v-else>&starf;</span>
                &nbsp;</a></td>
                <td>{{message.from.name}}<br/><small>{{message.date.fromNow()}}</small></td>
                <td>{{message.subject}}</td>
                <td> <span v-if="message.attachments.length > 0">&#128206;</span> </td>
            </tr>
        </tbody>
    </table>
    <p v-else>No messages in here yet</p>
</template>
<script>
import {eventBus} from "../main";

export default {
    props: {
        messages: {
            type: Array,
            required: true
        }
    },
    methods: {
        openMessage(message) {
            eventBus.$emit('changeView', {
                tag: 'view-message',
                title: message.subject,
                data: {
                    message: message
                }
            });
        }
    }
}
</script>