<template>
    <div class="card mb-5">
        <div class="card-body">
            <h6 class="card-subtitle mb-2 text-muted">{{data.message.from.name}}<br/><small class="text-muted">{{data.message.from.email}}</small></h6>

            <button v-if="data.message.isRead !== 'undefined' ? data.message.isRead == true : null" @click="data.message.isRead = false" type="button" class="btn btn-link float-right">&#10074; Mark as unread </button>
            <p class="card-text"><span class="container" v-html="data.message.content"></span></p>

            <a class="btn btn-secondary btn-sm" href="#" role="button" @click.prevent="navigate"> &#8678; Back </a>

            <a v-if="data.message.attachments.length > 0" class="btn btn-secondary btn-sm dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"> &#8681; Attachments </a>

            <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
                <a v-for="attachment in data.message.attachments" class="dropdown-item" href="#" :key="attachment.fileName">{{attachment.fileName}} (<small>{{attachment.size | formatBytes}}</small>)</a>
            </div>
            &nbsp;&nbsp;
            <a v-if="!data.message.isDeleted" class="btn btn-danger btn-sm" href="#" role="button" @click.prevent="data.message.isDeleted = true"> &#10006; Delete </a>
        </div>
        <div class="card-footer text-muted">{{data.message.date.fromNow()}}</div>
    </div>
</template>
<script>
import { eventBus } from "../main";

export default {
    props: {
        data: {
            type: Object,
            required: true
        }
    },
    activated() {
        if (typeof this.data.message.isRead !== 'undefined') this.data.message.isRead = true
    },
    filters: {
        formatBytes(bytes) {
            let deci = 2;
            let kilo = 1000;
            let dm = deci + 1 || 3;
            let size = ['Bytes', 'KB', 'MB', 'GB'];
            let i = Math.floor(Math.log(bytes) / Math.log(kilo));

            return `${parseFloat((bytes / Math.pow(kilo, i)).toFixed(dm))} ${size[i]}`
        }
    },
    methods: {
        navigate() {
            eventBus.$emit('changeView', {
                tag: this.$parent.previousView.tag,
                title: this.$parent.previousView.title,
                data: this.$parent.previousView.data,
            });
        }
    }
}
</script>