<template>
    <div class="container">
        <app-messages :messages="inboxMessages"></app-messages>
    </div>
</template>
<script>
import Messages from "../Messages.vue";
import { eventBus } from "../../main";

export default {
    props: {
        data: {
            type: Object,
            required: true
        }
    },
    computed: {
        inboxMessages() {
            return this.data.messages.filter(message => message.type == 'incoming' && !message.isDeleted)
        }
    },
    components: {
        'app-messages': Messages
    }
}
</script>