<template>
    <form @submit.prevent="sendMessage">
        <div class="form-group row">
            <label for="input-subject" class="col-sm-2 col-form-label">Subject</label>
            <div class="col-sm-10">
                <input type="text" class="form-control" id="input-subject" aria-describedby="subjectHelp" v-model="message.subject">
                <small id="subjectHelp" class="form-text text-muted">Try to stick to the topic. Focus on what you want to say, and give it a name, here.</small>
            </div>
        </div>
        <div class="form-group row">
            <label for="input-message" class="col-sm-2 col-form-label">Message</label>
            <div class="col-sm-10">
                <textarea class="form-control" id="input-message" rows="6" aria-describedby="contentHelp" v-model="message.content"></textarea>
                <small id="contentHelp" class="form-text text-muted">Next, lay it out clear and honest with your audience. Don't deviate, stay focused on the subject.</small>
            </div>
        </div>
        <button type="submit" class="btn btn-primary float-right mb-5">Send</button>
    </form>
</template>
<script>
import { eventBus } from "../main";
import moment from "../assets/js/moment";

export default {
    data() {
        return {
            message: {
                subject: '',
                content: ''
            }
        }
    },
    methods: {
        sendMessage() {
            eventBus.$emit('sentMessage', {
                message: {
                    subject: this.message.subject,
                    content: this.message.content,
                    isDeleted: false,
                    type: 'outgoing',
                    date: moment(),
                    from: {
                        name: 'Eugene Van Zyl',
                        email: 'eugevanz@gmail.com'
                    },
                    attachments: []
                }
            });
        }
    }
}
</script>